@import url(https://fonts.googleapis.com/css2?family=Handlee&display=swap);
body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

  background: linear-gradient(180deg, rgb(191,205,224,1) 0%, rgba(11,57,84,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  position: relative;
  
  text-align: center;
  margin: 0;
}

.style_container__2uN0W {
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
    padding: 2rem;
    font-family: 'Handlee', cursive;
}

h1 {
    margin: 0;
    font-size: 4.5rem;
    text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
                 0px 8px 13px rgba(0,0,0,0.1),
                 0px 18px 23px rgba(0,0,0,0.1);
}

@media(max-width: 769px) {
    h1 {
        font-size: 2.5rem;
    }
}
.style_board__DVKOT {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 2em 2em;
    background-color: #FFF;
    border-radius: 0 10px 10px 0;
    box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
    
    position: fixed;
    animation: style_slideRight__10vvO .5s ease;
}

.style_board__DVKOT p {
    margin: 0;
}

@keyframes style_slideRight__10vvO {
    from {
        transform: translateX(-35px)
    };
    to {
        transform: translateX(0px)
    };
}

@media (max-width: 767px) {
    .style_board__DVKOT{
        bottom: 5px;
        padding: 1em;
    }
}
.style_container__2G2Cs {
    font-size: 3rem;

    display:block;
    justify-content: center;
    align-items: center;
  
    height: 90vh;
    margin: 5rem 20%;
}

.style_wrapper__21E0I {
    display: grid;
    grid-template-columns: repeat(5, 20%);
    grid-template-rows: repeat(4, 25%);
    grid-gap: .2rem;
    justify-content: center;
    align-items: center;

    padding: 2rem;
    border-radius: 4px;

    background-color: #F5E6E8;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

.style_card__2Ot9s, .style_cardFlip__2jEi_ {
    background-color: #80ada0;
    color: #000;

    text-align: center;

    padding: .7em 1em;
    border-radius:4px;
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;

    display:flex;
    align-items: center;
    justify-content: center;

    cursor: pointer;

    transform-style: preserve-3d;
    transition: all .5s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.style_cardFlip__2jEi_ {
    transform: rotateY(180deg);
}

.style_card__2Ot9s:hover {
    transform: scale(1.02);
}

.style_back__2e0Id {
  transform: rotateY(180deg);
}

@media(max-width:1000px){
    .style_container__2G2Cs{
        font-size: 2.5em;
        margin: 5rem 10%;
    }
}

@media (max-width: 767px) {
    .style_container__2G2Cs{
        margin: 5rem 8%;
    }

    .style_wrapper__21E0I {
        padding: 1rem;  
    }
}

@media(max-width: 500px){
    .style_container__2G2Cs{
        font-size: 1.5em;
        margin: 5rem 3%;
    }
}
.style_modalOpen__yQqea {
    
    position: fixed;
    z-index: 999;
    top: 0;

    display: none; /* change to flex */
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;

    background: rgba(3, 3, 3, 0.466);
    padding: 2em;
}

.style_modalWrap__1e8cq {
    background: rgb(132, 219, 186);
    font-size: 1.5em;

    padding: 2em 4em;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}
